@use "../../lib/styles/" as *;
.SquareButton {
	@include textBoxPadding();
	font-size: 1.5rem;
	font-weight: 1000;
	text-transform: uppercase;
	cursor: pointer;
	background-color: white;
	text-align: center;
	user-select: none;
	color: black;
	border: 1px solid black;
	white-space: nowrap;

	&:hover {
		color: white;
		background-color: black;
		border: 1px solid white;
	}
}
